* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  border: 1px solid #0d161b;
  display: block;
}

h1 {
  margin: 0;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.breed-select {
  max-width: 400px;
  margin: auto;
  display: none;
}

.loader {
  width: 250px;
  justify-content: space-between;
  margin: 20% auto;
  font-size: 0;
  display: flex;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  width: 80px;
  height: 100px;
  background-color: #e5f3f2;
  background-image: radial-gradient(14px, #0d161b 100%, rgba(0, 0, 0, 0) 0);
  background-repeat: no-repeat;
  border: 1px solid #0d161b;
  border-radius: 50%;
  animation: eyeMove 10s infinite, blink 10s infinite;
  display: inline-block;
}

@keyframes eyeMove {
  0%, 10% {
    background-position: 0 0;
  }

  13%, 40% {
    background-position: -15px 0;
  }

  43%, 70% {
    background-position: 15px 0;
  }

  73%, 90% {
    background-position: 0 15px;
  }

  93%, 100% {
    background-position: 0 0;
  }
}

@keyframes blink {
  0%, 10%, 12%, 20%, 22%, 40%, 42%, 60%, 62%, 70%, 72%, 90%, 92%, 98%, 100% {
    height: 48px;
  }

  11%, 21%, 41%, 61%, 71%, 91%, 99% {
    height: 18px;
  }
}

.cat-info {
  background-color: #e5f3f2;
  border: 1px solid #0d161b;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  margin-top: 20px;
  padding: 20px;
  display: flex;
}

.box-img {
  align-items: center;
  display: flex;
}

.box {
  width: 400px;
}

